<template>
  <div align="center">
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ boardId }}</td>
      </tr>
      <tr>
        <th>글쓴이</th>
        <td>{{ userSeq }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ title }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ getFormatDate(regDt) }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ content }}
        </td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <router-link to="/board/list"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/board/update?boardId=' + boardId"
        ><button class="btn btn-primary" v-if="uid=='admin'">수정</button></router-link
      >
      <router-link :to="'/board/delete?boardId=' + boardId"
        ><button class="btn btn-primary" v-if="uid=='admin'">삭제</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import store from "@/store/store.js";

export default {
  name: 'detail',
  data: function() {
    return {
      uid: '',
    }
  },
  props: {
    boardId: { type: Number },
    userSeq: { type: String },
    title: { type: String },
    content: { type: String },
    regDt: { type: String },
  },
  methods: {
    getFormatDate(regDt) {
      return moment(new Date(regDt)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
  created() {
    this.uid = store.state.uid;
  },
};
</script>
