<template>
  <div>삭제중...</div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'delete',
  created() {
    http
      .delete(`/board/${this.$route.query.boardId}`)
      .then(({ data }) => {
        let msg = '삭제 처리시 문제가 발생했습니다.';
        if (data == 1) {
          msg = '삭제가 완료되었습니다.';
        }
        console.log(data);
        alert(msg);
        this.$router.push('/board/list');
      })
      .catch(() => {
        alert('삭제 처리시 에러가 발생했습니다.');
      });
  },
};
</script>

